.add-todo-form__checkbox-block{display:flex;width:360px;color:rgba(0,0,0,.535);padding:15px 15px 15px 20px;text-transform:uppercase;font-size:14px;font-weight:600;border-bottom:1px rgba(0,0,0,.131) solid;cursor:pointer}.add-todo-form__complete-checkbox-text{color:inherit}.add-todo-form__complete-checkbox-text-selected{color:#02a02f}.add-todo-form__complete-checkbox{position:relative;right:4px;bottom:1px;width:16px;height:16px;appearance:none;border-radius:2px;outline:none;cursor:pointer;background-color:#f7e631cd}.add-todo-form__complete-checkbox:checked:before{content:"✓";position:absolute;left:1px;bottom:-2px;font-size:20px;font-weight:700;color:#02a02f}