.todo{position:relative;height:max-content;padding:0 20px 0 20px;background-color:#fff;overflow:hidden;transition:transform .3s;cursor:pointer}.todo__status-title-block,.todo__date-tag-block{display:flex;align-items:center}.todo__complete-checkbox{position:relative;top:1px;width:16px;height:16px;appearance:none;background:#f7e631e3;border-radius:2px;outline:none;cursor:pointer}.todo__complete-checkbox:checked:before{content:"✓";position:absolute;left:1px;bottom:-2px;font-size:20px;font-weight:700;color:#02a02f}.todo__todo-information{display:flex;align-items:center;justify-content:space-between;padding:15px 0;border-bottom:2px #eee14d75 solid}.todo__todo-status-block{display:flex;align-items:center}.todo__todo-label{display:flex;align-items:flex-start}.todo__todo-tittle-block{width:60%}.todo__todo-title{display:flex;align-items:center;font-weight:500;font-size:13.5px;margin-left:10px;padding-right:20px;word-wrap:break-word}.todo__todo-deadline-date{display:inline-block;width:80px;height:20px;text-align:center;font-size:14px;font-weight:700;color:rgba(0,0,0,.398)}.todo__tag{display:flex;align-items:center;justify-content:center;margin-left:30px;width:100px;height:30px;border:none;border-radius:5px;background-color:#5f535325;font-size:16px;font-weight:600;cursor:pointer;transition:transform .3s ease,opacity .3s ease}.todo:hover{transform:scale(1.02)}.todo::first-letter{text-transform:uppercase}.todo-deadline-date{position:relative}.todo-deadline-date:before{content:"";position:absolute;top:3px;right:85px;width:15px;height:15px;background-image:url("../../../images/calendar-logo.png");background-repeat:no-repeat;background-size:contain;opacity:.7}